* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  margin: 20px 20px;
}
*::before,
*::after {
  box-sizing: border-box;
}

#html {
  word-break: break-all;
}

#div1 {
  position: fixed;
 left:60%;
 transform:translateX(-50%);
 top:20px;
}

#div1::before {
  content: '';
  display: block;
  position: absolute;
}

#div1::after {
  content: '';
  display: block;
  position: absolute;
}

@media (max-width:500px) {
  #html {
    height: 50vh;
    overflow: auto;
  }

  #div1wrapper {
    height: 50vh;
  }

  #div1 {
    position: relative;
    top: 50px;
    right: 50px;
  }
}